import React, { useState } from "react";
import classes from "./Checkout.module.css";

const Checkout = (props) => {
  const [enteredInput, setEnteredInput] = useState();

  const enteredInputIsValid = enteredInput.trim().length !== 0;

  const inputChangeHandler = (event) => {
    setEnteredInput(event.target.value);
  };

  const formSubmitHandler = (event) => {
    event.preventDefault();
    if (!enteredInputIsValid) {
      return;
    }

    console.log(enteredInput);
  };

  const nameClasses = `${enteredInputIsValid} ? ${classes.control} : ${classes.control} ${classes.invalid}`;

  return (
    <form className={classes.form} onSubmit={formSubmitHandler}>
      <div className={nameClasses}>
        <label htmlFor="name">Your Name</label>
        <input type="text" id="name" onChange={inputChangeHandler} />
      </div>
      <div className={classes.control}>
        <label htmlFor="city">City</label>
        <input type="text" id="city" />
      </div>
      <div className={classes.control}>
        <label htmlFor="postal">Postal Code</label>
        <input type="text" id="post" />
      </div>
      <div className={classes.control}>
        <label htmlFor="village">Village/street</label>
        <input type="text" id="village" />
      </div>
      <div className={classes.actions}>
        <button type="button" onClick={props.onCancel}>
          Cancel
        </button>
        <button type="submit" className={classes.submit}>
          Confirm
        </button>
      </div>
    </form>
  );
};

export default Checkout;
